.container {
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container h1,
h2 {
  text-align: center;
}

.container li {
  display: flex;
  gap: 30px;
}

.container form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container input {
  margin-bottom: 10px;
  width: 100%;
}

.container button {
  width: fit-content;
}
